#vids {
	width: 100%;
	height: 100vh;
	padding: 20px 3vw;
	position: relative;
	h1 {
		font: bold 50px/1 'Sunflower';
		margin-bottom: 4rem;
	}
	> div {
		width: 100%;
		display: flex;
		gap: 2rem;
	}
	article {
		flex: 1;
		margin-top: 1rem;
		> p {
			font: bold 24px/1 'mooli';
		}
		> img {
			max-width: 100%;
			margin-top: 32px;
		}
	}
}
