#scroll_navi {
	position: fixed;
	top: 50%;
	right: 50px;
	transform: translateY(-50%);
	display: flex;
	gap: 30px;
	flex-direction: column;

	li {
		width: 10px;
		height: 10px;
		background: #e0e0e0;
		cursor: pointer;
		transition: 0.5s;

		&.on {
			height: 100px;
			background: #333;
		}
	}
}
